import React from "react";

export default class ContactForm extends React.Component {
    constructor(props) {
        super();
        this.submitForm = this.submitForm.bind(this);
    }

    render() {
        return (
            <form id="contactForm" action="https://formspree.io/f/mjvpwepe" method="POST" onSubmit={this.submitForm}
                className="mx-lg-auto" style={{ maxWidth: '400px' }}>
                <div className="form-group">
                    <label htmlFor="contactName">Name</label>
                    <input id="contactName" name="name" type="text" className="form-control" required />
                </div>
                <div className="form-group">
                    <label htmlFor="contactEmail">Email</label>
                    <input id="contactEmail" name="email" type="_replyto" className="form-control" required />
                </div>
                <div className="form-group">
                    <label htmlFor="contactMessage">Message</label>
                    <textarea id="contactMessage" name="message" type="text" className="form-control" col="50" row="4" style={{ height: '200px' }} required />
                </div>

                <input type="text" name="_gotcha" style={{ display: "none" }} />
                <input type="hidden" name="_subject" value="New submission!" />

                <button id="submitButton" className="btn btn-primary my-3" type="submit">Submit</button>
            </form>
        );
    }

    submitForm(event) {
        event.persist();
        event.preventDefault();

        const form = event.target;

        if (!form.checkValidity()) {
            event.stopPropagation();
        }
        else {
            form.classList.add('was-validated');
            form.submit();
        }


    }
}